<template>
  <div>
    <div v-if="content !== 'Request failed with status code 401'">
      Test
    </div>
    <div v-else>
      Failed
    </div>
  </div>
</template>

<script>
  import UserService from "../services/user.service";

  export default {
    name: "UserTest",
    data() {
      return {
        content: null
      };
    },
    mounted() {
      UserService.getUserBoard().then(
          response => {
            this.content = response.data;
          },
          error => {
            this.content =
                (error.response && error.response.data && error.response.data.message) ||
                error.message ||
                error.toString();
          }
      );
    }
  }
</script>

